body,
html {
  font-family: "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: #091e42;
}

a {
  color: #007bff;
}

ul li {
  margin: 8px;
  line-height: 1.65;
}

b,
strong {
  font-weight: inherit;
  font-weight: bolder;
}

::selection {
  background: #80bdff;
  color: #333;
}

.img-res {
  max-width: 100%;
  height: auto;
}

p {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  letter-spacing: 0.03em;
}

h1 {
  font-size: 2rem;
  line-height: 1.23;
  font-weight: 200;
  letter-spacing: 0.01em;
}

@media (max-width: 599px) {
  h1 {
    font-size: 1rem;
  }
}

h2 {
  font-weight: 400;
}

h3 {
  font-weight: 400;
}

h4 {
  font-weight: 700;
}

h5 {
  font-weight: bold;
}

h6 {
  font-weight: bold;
}

p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.9;
  margin: 10px 0 20px;
  letter-spacing: -0.01em;
}

blockquote p {
  margin-bottom: 10px;
}

blockquote footer {
  display: block;
  font-size: 80%;
  color: #6c757d;
}

blockquote footer::before {
  content: "\2014\00A0";
}

pre, code {
  text-shadow: 0 1px white;
  font-family: "Operator Mono", "Fira Code", Consolas, Monaco, "Andale Mono", monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  font-size: 0.8rem;
  tab-size: 4;
  hyphens: none;
  line-height: 2;
  vertical-align: middle;
  padding: 10px;
  background: rgba(238, 238, 238, 0.35);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.125);
  border-radius: 3px;
}

code {
  line-height: 1.7;
  border-radius: 3px;
}

pre code, code code {
  box-shadow: none;
  background: none;
}

:not(pre):not(code) > code {
  background: #f0f2f1;
  padding: 1px 5px;
  color: #0062cc;
}

